.price__wrapper {
  position: relative;
  width: 100%;
  height: 35px;
}

.price__scale {
  position: absolute;
  width: 100%;
  height: 1px;
  top: 8px;
  background-color: $accent-bg-light;
}

.price__bar {
  position: absolute;
  width: 45%;
  height: 1px;
  top: 0;
  left: 33%;
  background-color: $accent-main;
}

.price__toggle {
  position: absolute;
  width: 15px;
  height: 15px;
  cursor: pointer;
  background-color: $accent-main;
  border-radius: 50%;

  & span {
      position: absolute;
      top: 15px;
      left: -7px;
      font-size: 14px;
      line-height: 130%;
    }

  &--min {
    left: 27.5%;
  }

  &--max {
    left: 77.5%;
  }
}
