.page-header {
  @media (max-width: $medium-width) {
    height: min-content;
    background-color: transparent;
    color: $basic-content-dark;

    .nojs & {
      background-color: $accent-main;
      color: $basic-white;
    }

    &--close {
      background-color: transparent;
      color: $basic-content-dark;
    }

    &--open {
      height: 100vh;
      overflow-y: auto;
      background-color: $accent-main;
      color: $basic-white;
    }
  }
}

.page-header__toggle {
  display: none;

  @media (max-width: $medium-width) {
    display: block;
    width: 25px;
    height: 16px;
    padding: 0;
    cursor: pointer;
    border: none;
    color: inherit;
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 25px 16px;

    &:hover {
      stroke: $accent-bg-opacity07;
      }

    &:active {
      stroke: $accent-bg;
    }
  }
}
